import React ,{useState} from 'react';

const Contact = () => {
  const [cha ,setCha]=useState({
    email:"",
    text:"",
  });
  const change =(e)=>{
    console.log(e.target.value);
    console.log(e.target.name);
    const value=e.target.value;
    const name=e.target.name;
    // const {value,name}=e.target;
    setCha((previouse)=>{
   
      return{
        ...previouse ,
        [name]:value,
      };
    });
   
  };
  const prev =(e)=>{
    e.preventDefault();
    alert(`${cha.email}.${cha.text}`)
    setCha('')
    };
    return (
        <>
        
          <div id="contact" className="container d-flex flex-column justify-content-center align-items-center" data-aos="fade-right">
              <p className="Heading"> Contact Us</p>
              <p>{cha.email}</p>
              <p>{cha.text}</p>
              
          <div className=" col-8 mb-3" >
         
  <label for="exampleFormControlInput1" style={{ fontSize:"2rem"}}className="form-label" >Email address</label>
  <input type="email" className="form-control"  style={{ fontSize:"2rem"}} value={cha.email} name="email" id="exampleFormControlInput1"  onChange={change} placeholder="name@example.com" />
</div>
<div className="col-8 mb-3">
  <label for="exampleFormControlTextarea1" style={{ fontSize:"2rem"}} className="form-label">Example textarea</label>
  <textarea className="form-control" style={{ fontSize:"2rem"}} name="text" value={cha.text} id="exampleFormControlTextarea1" rows="3" onChange={change}></textarea>
  <button type="button" class="btn btn-zee btn-info my-3" onClick={prev}>Submit</button>

</div>

          </div>
       

        </>
    )
}
export default Contact;