import React from 'react';

export const Card = (props) => {
    return (
        <>
            <div class="card m-3 card-clip"  style={{width: "18rem" , height:"100%" , fontSize:"1.5rem"}}>
                
  <img src={props.img} class="card-img-top" alt="..." />
  <div class="card-body">
    <h5 class="card-title sub_Heading " style={{lineHeight:"2.4rem"}}>{props.info}</h5>
    <p class="card-text">Lorem ipsum dolor sit amet consectetur adipisicing elit. Impedit ullam id sapiente? Mollitia incidunt quaerat eum. Nisi fugit illo enim.</p>

  </div>
</div>
        </>
    )
}
export default Card;