import React from 'react'

export const Landing = () => {
    return (
        <>
        <section className="container-fluid landing  " style={{background: `url("./images/hero.jpg")`,backgroundSize:"cover" ,backgroundRepeat:"no-repeat" }}>
        <div className="container landing_dt" data-aos="fade-up"
     data-aos-anchor-placement="top-bottom" >
            <h1 className="Heading" data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="900"
     data-aos-offset="0"> Welcome</h1>
            <p className="sub_Heading" data-aos="zoom-in">The Rosa Hotel</p>
            <p className="description" data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="900"
     data-aos-offset="0">Ready to be opened</p>
            <button className="btn btn-danger" data-aos="fade-zoom-in"
     data-aos-easing="ease-in-back"
     data-aos-delay="900"
     data-aos-offset="0">Explore</button>
           
           
        </div>
        </section>
        </>
    )
}
export default Landing;
