import React from 'react'

export const Navbar = () => {
    return (
        <>
      
<nav className="navbar navbar-expand-lg" > 
  <div className="container">
    <a className="navbar-brand" >RosaFood</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="#Menu">Menu</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#desert">Desert</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#ourstory">Our Story</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href="#contact">Contact</a>
        </li>
      
       
      </ul>
      <form className="d-flex flex-wrap">
     
        <button className="btn btn-outline-success btn-zee" type="submit">Login</button>
        <button className="btn btn-outline-success btn-zee" type="submit">Sign Up</button>
      </form>
    </div>
  </div>

</nav>
</>
    )
}
export default Navbar;
